import React, {useState} from 'react';
import Title from "../../../components/title";
import GridView from "../../../containers/grid-view";
import {KEYS} from "../../../constants/keys";
import {URLS} from "../../../constants/urls";
import {get, head, last, split} from "lodash"
import downloadIcon from "../../../assets/icons/download.svg"
import {useNavigate} from 'react-router-dom'
import {NumericFormat} from "react-number-format";
import cloudIcon from "../../../assets/icons/Folders.svg";
import monitorIcon from "../../../assets/icons/monitor.svg";
import {useTranslation} from "react-i18next";
import {usePostQuery} from "../../../hooks/api";
import dayjs from "dayjs";
import {RefreshCcw} from "react-feather";
import {ContentLoader} from "../../../components/loader";
import Form from "../../../containers/form";
import {Datepicker} from "../../../containers/form/components";
import Modal from "../../../components/modal";


const InpatientReportsContainer = () => {
    const [open, setOpen] = useState(false)
    const navigate = useNavigate()
    const {t} = useTranslation()
    const {
        mutate: refreshReportRequest,
        isLoading: isLoadingRefreshReport
    } = usePostQuery({listKeyId: KEYS.inpatientReports})

    const columns = [
        {
            title: t('Month'),
            key: 'month',
        },
        {
            title: t('СЛУЧАИ ФГМС'),
            key: 'fgms_count',
            render: ({value}) => <NumericFormat thousandSeparator={" "} displayType={'text'} value={value}/>
        },
        {
            title: t('СУММА ДЛЯ ОПЛАТЫ'),
            key: 'fgms_payment',
            render: ({value}) => <NumericFormat thousandSeparator={" "} displayType={'text'} value={value}/>
        },
        {
            title: 'ДРУГИЕ СЛУЧАИ',
            key: 'total_count',
            render: ({row}) => <NumericFormat thousandSeparator={" "} displayType={'text'} value={get(row,'total_count',0)-get(row,'fgms_count',0)}/>
        },
        {
            title: 'СУММА',
            key: 'total_payment',
            render: ({row}) => <NumericFormat thousandSeparator={" "} displayType={'text'} value={get(row,'total_payment',0)-get(row,'fgms_payment',0)}/>
        },
        {
            title: t('ДЕЙСТВИЯ'),
            key: 'organization_level',
            classnames: '!text-center',
            render: ({row}) => <div className={'flex justify-around items-center px-3'}>
                <img className={'mr-1'} src={cloudIcon} alt="cloud"/>
                <img src={monitorIcon} alt="cloud"/>
            </div>
        }

    ]

    const refreshReport = ({data: {month = new Date()} = {}}) => {
        refreshReportRequest({
            url: URLS.inpatientReports,
            attributes: {
                month: dayjs(month).format("YYYY-MM")
            }
        }, {
            onSuccess: () => {
                setOpen(false);
            }
        })
    }
    return (
        <div>
            <div className="grid grid-cols-12 items-center">
                <div className="col-span-8">
                    <Title>{t("Стационарные отчеты")}</Title>
                </div>
                <div className="col-span-4 text-right">
                    <button
                        onClick={() => setOpen(true)}
                        className={'inline-flex items-center py-2.5 pl-2.5 pr-5 text-sm font-bold text-primary border border-primary rounded-lg mr-3'}>
                        <RefreshCcw className={'mr-2.5'} size={20}/>
                        {t("Generate")}
                    </button>
                    <button
                        className={'inline-flex items-center py-2.5 pl-2.5 pr-5 text-sm font-bold text-primary border border-primary rounded-lg'}>
                        <img className={'mr-2.5'} src={downloadIcon} alt={'export'}/>
                        {t("Export")}
                    </button>
                </div>
                <div className="col-span-12 mt-8">
                    <GridView
                        onRowClick={(_row) => navigate(`/hospitals/inpatient-reports/${head(split(get(_row, 'month', ""), '/', 2))}/${last(split(get(_row, 'month', ""), '/', 2))}`)}
                        listKey={KEYS.inpatientReports} url={URLS.inpatientReports}
                        columns={columns}/>
                </div>
            </div>
            <Modal onClose={() => {
                setOpen(false);
            }}
                   classNames={'overflow-y-visible relative'}
                   title={''}
                   open={open}>
                {isLoadingRefreshReport && <ContentLoader/>}
                <Form onSubmit={refreshReport}>
                    <Datepicker property={{showMonthYearPicker: true}} dateFormat={'yyyy/MM'} name={'month'}
                                label={t('Month')}
                                params={{required: true}}/>
                    <button type={'submit'}
                            className={'w-full p-3.5 rounded-lg bg-primary block  text-white font-bold text-center h-12 mt-6'}>
                        {t('Generate')}
                    </button>
                </Form>
            </Modal>
        </div>
    );
};

export default InpatientReportsContainer;